<template>
    <canvas :id=properties.id data-paper-hidpi="off"></canvas>
</template>
<script>
export default {
    props:['properties', 'parameters'],
    mounted: function(){
        let canvas = document.getElementById(this.properties.id);
        canvas.width = this.properties.width;
        canvas.height = this.properties.height;
        paper.setup(canvas);
        
        let pointRadius = canvas.width / 40;
        let xMin = pointRadius;
        let xMax = canvas.width - pointRadius;
        let xRange = xMax - xMin;
        let yMin = pointRadius;
        let yMax = canvas.height - pointRadius;
        let yRange = yMax - yMin;

        let infectionProbability = 0.1;



        let nPoints = 100;
        let points = [];
        let collisions = [];

        //let centrePoint = new Path.Circle(canvas.width /2, canvas.height / 2, 10);
        //centrePoint.style.fillColor = 'black';


        let getCollisionState = function(positionA, positionB, collisionRange) {
            let xDistance = Math.abs(positionA.x - positionB.x);
            let yDistance = Math.abs(positionA.y - positionB.y);
            //console.log(xDistance + ', ' + yDistance)
            if(xDistance > collisionRange || yDistance > collisionRange) return false;
            //return true;
            return ((xDistance ^ 2) + (yDistance ^ 2) < (collisionRange ^ 2));
        }

        let handleCollision = function(pointA, pointB, areColliding) {
            let bothPoints = [pointA, pointB];
            let infectedPoints = [];
            let uninfectedPoints = [];
            if(areColliding){
                bothPoints.forEach(point => {
                    point.isColliding = true;
                    if(point.isInfected) {
                        infectedPoints.push(point);
                    } else {
                        uninfectedPoints.push(point);
                    }
                });
                if(infectedPoints.length === 1) {
                    uninfectedPoints[0].isInfected = Math.random() < infectionProbability;
                }
            }

            let areAnyInfected = infectedPoints.length > 0;
            bothPoints.forEach(point => {
                if(areAnyInfected) point.isNearInfected = true;
                point.circle.style = Object.assign(getCollisionStyle(point.isColliding, point.isNearInfected), point.circle.style);
                point.circle.style = Object.assign(getInfectionStyle(point.isInfected), point.circle.style);
            });

        }
        
        let getInfectionStyle = function(isInfected) {
            return {
                fillColor: isInfected ? 'pink': 'white'
            }
        }

        let getCollisionStyle = function(isColliding, isNearInfected) {
            return {
                strokeColor: isColliding ? (isNearInfected ? 'red' : 'black'): 'grey',
                strokeWidth: isColliding ? pointRadius * 0.4 : pointRadius * 0.4
            }
        }

        let nInfectedInitial = 1;

        for(let i = 0; i < nPoints; i++){
            let x =  xMin + Math.random() * xRange;
            let y =  yMin + Math.random() * yRange;
            let point = {
                speed: Math.random() * 2.5,
                circle: new Path.Circle(x, y, pointRadius),
                isColliding: false,
                isInfected: i < nInfectedInitial,
                isNearInfected: false
            }
            point.circle.style = getInfectionStyle(point.isInfected);
            points.push(point);
        }

        view.onFrame =function(event) {
            points.forEach((point, i) => {
                let nCollisions = 0;
                point.isColliding = false;
                point.isNearInfected = false;
                for(let j = 0; j < i; j++) {
                    let pointA = point;
                    let pointB = points[j];
                    let areColliding = getCollisionState(pointA.circle.position, pointB.circle.position, pointRadius * 2);
                    handleCollision(pointA, pointB, areColliding);
                }

                let delta = event.delta;
                let vector = new Point(0, 1 * delta * point.speed * 100);
                vector.angle = 360 * Math.random();
                let oldPosition = point.circle.position;
                let newX = oldPosition.x + vector.x;
                newX = newX < xMin ? xMin + (xMin - newX) : newX > xMax ? xMax - (newX - xMax) : newX;
                let newY = oldPosition.y + vector.y;
                newY = newY < yMin ? yMin + (yMin - newY) : newY > yMax ? yMax - (newY - yMax) : newY;
                point.circle.position = [newX, newY]
            });
        }
        view.draw();
    

    }
}

</script>
