<template>
  <div id="app">
    <div id="rotator">
      <div class="half-holder" ref="parent">
        <Particles v-if="isReady" v-bind:properties=getParticlesProps() v-bind:parameters=getParticlesParams()></Particles>
      </div>
      <div class="half-holder" v-if="isReady">
        <Slider  v-for="params in getSliderParams()" v-bind:key="params.key" 
                 v-bind:parameters="params" v-bind:properties="getSliderProps(params)"></Slider>
      </div>
    </div>
  </div>
</template>

<script>
import Particles from './components/Particles.vue';
import Slider from './components/Slider.vue';
import paper from 'paper';

export default {
  name: 'App',
  components: {
    Particles, Slider
  },
  data: function(){
    return {
      isReady: false,
      params: this.initializeParams()
    }
  },
  methods: {
    initializeParams() {
      //This is necessary because we want to create a new 'resetValue' variable based on 'value'.
      let params = [
        {'key':'speed', 'title': 'Speed', 'value': 5, 'min': 0, 'max': 10 },
        {'key':'population', 'title': 'Population', 'value': 20, 'min': 1, 'max': 100 },
        {'key':'mortality', 'title': 'Mortality', 'value': 8, 'min': 0, 'max': 10 },
        {'key':'contagiousness', 'title': 'Contagiousness', 'value': 20, 'min': 1, 'max': 100 },
        {'key':'speed_range', 'title': 'Speed Range', 'value': 45, 'min': 0, 'max': 100 },
        {'key':'etc1', 'title': 'Population', 'value': 50, 'min': 1, 'max': 200 },
        {'key':'etc2', 'title': 'Speed', 'value': 1, 'min': 0, 'max': 10 }
      ]
      params.forEach(param => {
        param.resetValue = param.value;
      });
      return params;
    },
    getParticlesProps(){
      return {
        id: 'particles',
        width: this.divWidth * 0.8, //Math.min(this.divWidth, this.divHeight),
        height: this.divHeight * 0.8, //Math.min(this.divWidth, this.divHeight),
        isReady: this.isReady
      }
    },
    getSliderProps(params){
      let props = Object.assign(params, {
        id: params.key + '-slider',
        width: this.divWidth * 0.8,
        height: this.divHeight / 5,
        weights: {
          paddingTop: 2,
          paddingBottom: 0,
          titleFontSize: 2,
          paddingMiddle: 1,
          slotWidth: 3
        },
      });
      return props;
    },
    getSliderParams(){
      return this.params;
    },
    getParticlesParams(){
      let particlesParams = {}
      this.params.forEach(param => {
        particlesParams[param.key] = param;
      });
      console.log(particlesParams);
      return particlesParams;
    }
  },
  mounted: function(){
    this.canvasDimension = 0.9 * Math.min(window.innerWidth, window.innerHeight);

    this.parentDiv = this.$refs['parent'];

    paper.install(window);
    console.log(window.paper);
    this.isReady = true;
    this.field = 'speed';

    this.divWidth = this.$refs.parent.offsetWidth;
    this.divHeight = this.$refs.parent.offsetHeight;

  }
}
</script>

